import React, { Component } from 'react';
import { connect } from 'react-redux';

import * as d3 from 'd3';

const data = '/static/northern.csv';

export default class RussianGamingGraph extends Component {
  constructor(props, context) {
    super(props, context);
    this.props = props;

    this.visualize = this.visualize.bind(this);
    console.log('HousingGraph constructor this.props: ', this.props);
  }

  componentWillMount() {}

  componentDidMount() {
    console.log('HousingGraph componentDidMount');
    this.visualize();
  }

  componentDidUpdate() {
    console.log('HousingGraph componentDidUpdate');
  }

  visualize() {
    console.log('drawActivity');

    const tooltip = d3
      .select('body')
      .append('div')
      .attr('id', 'tooltip')
      .style('width', '150px')
      .style('height', '40px')
      .style('position', 'absolute')
      .style('z-index', '10')
      .style('visibility', 'hidden')
      .style('padding', '10px')
      .style('background-color', 'white')
      .style('opacity', '0.9');

    const tooltipG = tooltip.append('svg').append('g');

    tooltipG
      .append('text')
      .attr('x', 0)
      .attr('y', 20)
      .attr('class', 'tooltipFirstText')
      .text('a simple tooltip');

    tooltipG
      .append('text')
      .attr('x', 0)
      .attr('y', 40)
      .style('fill', '#999')
      .attr('class', 'tooltipSecondText')
      .style('font-size', '12px')
      .text('a simple tooltip 2');
    /*
    tooltipG
      .append('rect')
      .attr('width', 100)
      .attr('height', 5)
      .attr('y', 60)
      .attr('class', 'tooltipBar')
      .style('fill', 'red');
      */

    const width = window.innerWidth;
    const height = window.innerHeight;
    const viewport = d3
      .select('#viewport')
      .append('svg')
      .attr('width', width)
      .attr('height', height)
      .style('margin', '0 auto');

    const svg = viewport;

    d3
      .select('svg')
      .selectAll('.node')
      .remove();

    const format = d3.format(',d');

    const color = '#FF0'; //d3.scaleOrdinal(d3.schemeCategory20c);

    const pack = d3
      .pack()
      .size([width, width])
      .padding(1.5);

    d3.csv(data).then(d => {
      d.value = +d.value;
      if (d.value) return d;
    });
  }

  render() {
    return <div id="viewport" />;
  }
}



